import { Direction } from "@tanyoknits/shared";
import SectionInstruction from "@tanyoknits/shared/src/components/SectionInstruction";

interface Props {
  needle: string;
  method: string;
  direction: Direction;
  coCount: number;
}

export default function COInstruction({
  needle,
  method,
  direction,
  coCount,
}: Props) {
  return (
    <SectionInstruction
      intro={`🪡 With a ${needle} needle, use the ${method} method`}
      rows={[
        {
          text: `CO ${coCount} ${
            direction === "round" ? "in the rnd" : "flat"
          }`,
        },
      ]}
      rowType={"CO"}
      afterList={[
        direction === "round"
          ? "Pm at the end to indicate BOR (middle back)"
          : "",
      ]}
    />
  );
}
