import { getTruelyEven } from "@tanyoknits/truly-even";
import { InstructionProps } from "./Instruction";
import { SectionInstruction, getInchAndCm } from "@tanyoknits/shared";

export default function BodyInstruction({
  schema,
  measurement,
}: InstructionProps) {
  const endSts = schema.total.body - schema.waistDec;
  const decText = getTruelyEven(schema.total.body / 2, endSts / 2, {
    avoidEnds: true,
    sts: "ssk-k2tog",
    count: 2,
    hasDoubleNest: true,
  });
  const startRowID = 2 + schema.raglan.every2 * 2 + schema.raglan.every4 * 4;

  return (
    <>
      <SectionInstruction
        title="Body & sleeves split"
        rowType={`Rnd ${startRowID + 1} + CO`}
        rows={[
          {
            text: `k till mA, rm mA, slip sts to scrap yarn or stitch holder till mB, rm mB, CO ${schema.underarm}, k incl chart B till mC, 
          slip sts in scrap yarn or stitch holder till mD, CO ${schema.underarm}, k till BOR`,
            notes: `total ${schema.total.body} sts`,
          },
        ]}
      />
      <SectionInstruction
        rowType="Rnd"
        startRowID={startRowID + 1}
        rows={[{ text: "k all incl chart B" }]}
        notes={`Rep the last row till appx. ${getInchAndCm(
          measurement.hem + 0.25
        )} shorter than the desired length.`}
      />

      <SectionInstruction
        title="Waistline dec"
        rowType="Next rnd"
        rows={[
          {
            text: `{${decText.sts.join(", ")}}, rep{-}`,
            notes: `${schema.waistDec} sts dec'd, total ${endSts} sts`,
          },
          { text: "change to a smaller needle, k all" },
        ]}
      />
      <SectionInstruction
        title="Hem ribbing"
        rowType="Next rnd"
        rows={[
          { text: `(k1, p1), rep (-) to end (${endSts / 2} times)` },
          { text: "as rnd 1" },
        ]}
        notes={`Work in ribbing for ${getInchAndCm(
          measurement.hem
        )}. BO with Sewn-on BO is recommended.`}
      />
    </>
  );
}
