import { getInchAndCm } from "@tanyoknits/shared";
import { RowType } from "@tanyoknits/shared";
import SectionInstruction from "@tanyoknits/shared/src/components/SectionInstruction";

interface Props {
  part: "Neck band" | "Hem";
  rib: [number, number];
  sts: number;
  length: number;
  rowType?: RowType;
  needle?: string;
}

export default function RibInstruction({
  part,
  needle,
  rib,
  sts,
  length,
  rowType,
}: Props) {
  return (
    <SectionInstruction
      title={`${part} ${rib[0]}x${rib[1]} ribbing`}
      intro={needle != null ? `🪡 Change to needle size ${needle}` : ""}
      rowType={rowType}
      startRowID={0}
      rows={[
        {
          text: `(k${rib[0]}, p${rib[1]}), rep (-) to end (${
            sts / (rib[0] + rib[1])
          } times)`,
        },
        { text: `as rnd 1` },
      ]}
      notes={`Work in ribbing for ${getInchAndCm(length)}, reset row count.`}
    />
  );
}
