import {
  getDec2StsList,
  getChangeStsList,
  getStsListRepets,
} from "@tanyoknits/shared";

export type IncStsType = "m1-m1" | "m1r-m1l" | "m1l-m1r";
export type DecStsType =
  | "k2tog-k2tog"
  | "ssk-ssk"
  | "k2tog-ssk"
  | "ssk-k2tog"
  | "cdd-cdd-k2tog"
  | "k3tog-k3tog-k2tog"
  | "sk2p-sk2p-ssk"
  | "k3tog-sk2p-ssk"
  | "sk2p-k3tog-k2tog";

export interface OptionType {
  avoidEnds: boolean;
  sts: DecStsType | IncStsType;
  count: number;
  hasDoubleNest: boolean;
}

// Get truly even increase or decrease instruction
export function getTruelyEven(
  startSts: number,
  endSts: number,
  option: OptionType
): { sts: string[]; avoidEndAvailable: boolean; doubleNestAvailable: boolean } {
  let avoidEndAvailable = true;
  let doubleNestAvailable = false;

  // Inc/Dec stitch point list
  const stsPoints =
    option.count === 3
      ? getDec2StsList(startSts, endSts)
      : getChangeStsList(startSts, endSts);
  const { avoidEnds, sts, count, hasDoubleNest } = option;

  // If users don't want to end with inc/dec at the end, start it early
  const tailSts = Math.floor((stsPoints[0] - count) / 2);
  if (tailSts === 0) {
    avoidEndAvailable = false;
  }
  if (avoidEnds) {
    stsPoints[0] -= tailSts;
  }

  // Generate text from all 'k' include increase or decreases stitches
  const incDecStsList = stsPoints.map((stsCount, i) => {
    const stsTypes = sts.split("-");
    return count === 3 &&
      i === stsPoints.length - 1 &&
      (endSts - startSts) % 2 !== 0 // orphan increase for 2 sts dec
      ? `k${stsCount - 2}, ${stsTypes[2]}`
      : `k${stsCount - count}, ${
          // For increase, count is 0 since it's adding a new M stitch
          i < stsPoints.length / 2 ? stsTypes[0] : stsTypes[1]
        }`;
  });
  if (avoidEnds && tailSts > 0) {
    incDecStsList.push(`k${tailSts}`);
  }

  // Repeated stitches are now presented with "X times"
  const stsMultiplied: string[] = getStsListRepets(incDecStsList);

  // Check multiple increase groups are repeated
  // e.g., [(k2, m1), (k3, m1) x 3] x 4
  let repeatCheckIdx = 0;

  const nested = stsMultiplied.reduce((labels: string[], sts, idx) => {
    // Skip sts that's already applied to the previous repeats
    if (idx < repeatCheckIdx) {
      return labels;
    }
    // Recursive: if repeats aren't found, increase the number of repeated sts
    // start with two (e.g., (k2, m1), (k3, m1) x 3)
    function checkRepeats(count: number): string[][] {
      const repeats = [stsMultiplied.slice(idx, idx + count)];
      for (let i = idx + count; i <= stsMultiplied.length - count; i += count) {
        // check if the following repeats are the same
        if (
          JSON.stringify(repeats[0]) ===
          JSON.stringify(stsMultiplied.slice(i, i + count))
        ) {
          repeats.push(repeats[0]);
          doubleNestAvailable = true;
        } else {
          break;
        }
      }
      if (repeats.length > 1 || count > stsMultiplied.length / 2) {
        return repeats;
      }
      return checkRepeats(count + 1);
    }

    const repeats = checkRepeats(2);

    if (repeats.length > 1) {
      const labelRepeats = repeats[0];
      labels.push(`[${labelRepeats.join(", ")}] x ${repeats.length}`);
      // Skip all repeated ones
      repeatCheckIdx += repeats.flat().length;
    } else {
      labels.push(sts);
      // Go to the next repeat
      repeatCheckIdx++;
    }
    return labels;
  }, []);

  return {
    sts: hasDoubleNest ? nested : stsMultiplied,
    avoidEndAvailable,
    doubleNestAvailable,
  };
}
