import {
  Abbr,
  Techniques,
  MotifInPattern,
  StitchList,
  definition,
  techniques,
  getChartWithNoGroups,
  getVerticalStackedChart,
} from "@tanyoknits/shared";
import { getMotif } from "@tanyoknits/shared/src/stitches/patterns/Pyramid";
import { PageInfo } from "../../utils/types";
import { Text, VStack } from "@chakra-ui/react";
import MotifBySize from "../../components/MotifBySize";
import { Size } from "../../utils/sizes";
import { sizes } from "./pattern";

export interface MotifBySizeItem {
  sizes: Size[];
  motif: MotifInPattern;
}

// Stitches in charts
const { k, c2l, c2r, m1l, lli, llip } = definition;

// Charts
const stacked = [
  getMotif(5, true),
  getMotif(4, true),
  getMotif(3, true),
  getMotif(2, true),
];

const motif1: MotifInPattern = {
  stsCount: stacked[1].stsCount,
  chart: getChartWithNoGroups(getVerticalStackedChart(stacked.slice(1, 4))),
  glyphSize: "M",
};
const motif2: MotifInPattern = {
  stsCount: stacked[0].stsCount,
  chart: getChartWithNoGroups(getVerticalStackedChart(stacked.slice(0, 3))),
  glyphSize: "M",
};

const sizeCutID = 6;
export const motifABySize: MotifBySizeItem[] = [
  // Add untitled title since they are an original motif
  {
    sizes: sizes.slice(0, sizeCutID),
    motif: { ...stacked[3], title: undefined },
  },
  { sizes: sizes.slice(sizeCutID), motif: { ...stacked[2], title: undefined } },
];

export const motifBBySize: MotifBySizeItem[] = [
  { sizes: sizes.slice(0, sizeCutID), motif: motif1 },
  { sizes: sizes.slice(sizeCutID), motif: motif2 },
];

const usedTechniques: {
  title: string;
  desc: string;
  video: string | undefined;
}[] = [
  {
    title: "Tubular CO",
    desc: "Used for stretch neckline cast on",
    video: techniques["Tubular CO"]?.videos[0].url,
  },
  {
    title: "m1l & m1r",
    desc: "Make 1 left/right, used for neckline increase",
    video: m1l.videos?.[0].url,
  },
  {
    title: "w&t",
    desc: "Wrap & turn; short row technique, my preference is German short row. ",
    video: techniques["Wrap & Turn"]?.videos?.[0].url,
  },
  {
    title: "LLI & RLI",
    desc: "Lifted increase to the left/right, used for raglan increase",
    video: lli.videos?.[0].url,
  },
  {
    title: "RLI-P & LLI-P",
    desc: "Lifted increase to the right/left side on the wrong side, used for raglan increase on the wrong side (odd number of short rows)",
    video: llip.videos?.[0].url,
  },
  {
    title: "LT & RT",
    desc: "Left/right twist, basically 2-sts cable done without cable needle",
    video: c2l.videos?.[0].url,
  },
  {
    title: "Cable CO",
    desc: "Recommended for CO while body and sleeve are split",
    video: techniques["Underarm CO"]?.videos[0].url,
  },
  {
    title: "Underarm pick up",
    desc: "Gapless underarm pick up, one of the useful tips is:",
    video: techniques["Underarm pick up"]?.videos[0].url,
  },
  {
    title: "Sewn on BO",
    desc: "Or tubular BO",
    video: techniques["Tubular BO"]?.videos[0].url,
  },
];

const stitchPage: PageInfo[] = [
  {
    id: "chart-A",
    label: "Chart A",
    component: (
      <VStack spacing={8} align="left">
        <Text>
          Following stitch pattern is incorporated at the center of sleeve end,
          right before cuff.
        </Text>
        <MotifBySize
          motifBySize={motifABySize.map((m) => {
            const { motif, sizes } = m;
            return { size: sizes.join(", "), motif, direction: "round" };
          })}
        />
        <StitchList stitches={[k, c2r, c2l]} />
      </VStack>
    ),
  },
  {
    id: "chart-B",
    label: "Chart B",
    component: (
      <VStack spacing={8} align="left">
        <Text>
          Following stitch pattern is incorporated at the center of front, after
          short row is complete. Chart B is a stacked variation of Chart A in
          three different sizes.
        </Text>
        <MotifBySize
          motifBySize={motifBBySize.map((m) => {
            const { motif, sizes } = m;
            return { size: sizes.join(", "), motif, direction: "round" };
          })}
        />
        <StitchList stitches={[k, c2r, c2l]} />
      </VStack>
    ),
  },
  {
    id: "techniques",
    label: "Techniques",
    component: <Techniques list={usedTechniques} />,
  },
  {
    id: "abbr",
    label: "Abbreviations",
    component: (
      <Abbr
        list={[
          "bef",
          "BO",
          "beg",
          "BOR",
          "CO",
          "dec",
          "foll",
          "inc",
          "pm*",
          "sm*",
          "rm*",
          "rep",
          "rnd",
          "SR",
          "tog",
          "WS",
          "RS",
          "cont",
          "incl",
          "appx",
        ]}
      />
    ),
  },
];

export default stitchPage;
